.page-main {
  background-color: #f8f8f8;
  text-align: left;
  border-top: 1px solid #d9fff5;
}

.page-main__client-buttons {
  display: none;
  @media (min-width: 1120px) {
    display: flex;
    font-family: "TT Lakes", sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    position: absolute;
    top: 42px;
    left: 716px;
  }
}

.client-buttons__item {
  display: flex;
  align-items: center;
  padding-right: 31px;
}

.client-buttons__image {
  padding-left: 10px;
}
