.info-block__title {
  margin-top: 24px;
  position: relative;
  font-family: "TTLakes", sans-serif;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  padding-left: 16px;

  @media (min-width: 768px) {
    padding-left: 24px;
  }

  @media (min-width: 1120px) {
    margin-top: 41px;
    margin-bottom: 0;
    padding-left: 33px;
  }
}

.info-block__title::before {
  content: "";
  position: absolute;
  width: 4px;
  height: 40px;
  top: -1px;
  left: -1px;
  background-image: url("../img/block-info-h-decoration.svg");
}

.contols-list__pre-desktop-content,
.contols-list__desktop-content {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  overflow: hidden;
  padding-left: 16px;

  @media (min-width: 768px) {
    padding-left: 24px;
    margin-top: 31px;
  }

  @media (min-width: 1120px) {
    padding-left: 33px;
    margin-top: 9px;
    flex-wrap: wrap;
  }
}

.contols-list__desktop-content {
  display: none;
}

.contols-list__item {
  font-family: "TTLakes", sans-serif;
  font-size: 16px;
  line-height: 24px;

  white-space: nowrap;
}
.contols-list__item button {
  letter-spacing: -0.1px;
}

.contols-list .contols-list__item:nth-child(n+2) {
  margin-right: 32px;
  @media (min-width: 1120px) {
  margin-right: 38px;
}
}

.contols-list__item--active {
  padding: 6px 17px 6px 16px;
  background-color: #ffffff;
  border: 2px solid #b8ffec;
  border-radius: 6px;
  margin-right: 16px;

  @media (min-width: 1120px) {
    padding: 6px 25px 6px 20px;
    margin-right: 19px;
  }
}

.info-block__text-block {
  margin-top: 10px;
  font-family: "TTLakes", sans-serif;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  padding: 0 16px 0 16px;

  @media (min-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 24px;
    padding-right: 48px;
    margin-top: 20px;
    line-height: 20px;
  }

  @media (min-width: 1120px) {
    padding-left: 35px;
    padding-right: 36px;
    margin-top: 6px;
  }
}

.text-block__text-limiter {
  height: 120px;
  overflow-y: hidden;
  @media (min-width: 768px) {
    height: 175px;
  }
}

.text-block .text-block__item:nth-child(2) {
  @media (min-width: 768px) {
    margin-top: 20px;
  }
}

.info-block__more-button {
  font-family: "TTLakes", sans-serif;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  margin-left: 32px;
  @media (min-width: 768px) {
    margin-top: 8px;
  }
  @media (min-width: 1120px) {
    margin-top: 13px;
  }
}

.info-block__more-button::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 10px;
  top: 8px;
  left: -24px;
  background-image: url("../img/block-info-read-more-decoration.svg");
  background-repeat: no-repeat;
  transform: rotate(0deg);
}
.info-block__text-wrapper {
  @media (min-width: 540px) {
    display: flex;
  }
}

.info-block__image {
  margin: 17px 0 35px 0;
  height: 176px;
  width: 320px;

  @media (min-width: 336px) {
    padding-left: 16px;
  }
  @media (min-width: 540px) {
    padding-left: 0;
    padding-right: 16px;
  }
  @media (min-width: 768px) {
    margin-top: 32px;
    border-radius: 5px;
    padding-right: 24px;
    min-width: 360px;
    height: 208px;
  }
  @media (min-width: 1120px) {
    height: 307px;
    min-width: 531px;
    padding-right: 29px;
    margin-top: 10px;
  }
}

@media (min-width: 1120px) {
  .contols-list__pre-desktop-content {
    display: none;
  }
  .contols-list__desktop-content {
    display: flex;
  }
  .contols-list__item:last-child {
    margin-top: 14px;
    padding-left: 23px;
  }
}
