.main-nav {
  display: flex;
  align-items: center;
}

.main-nav__logotype-image {
  padding-left: 16.73px;

  @media (min-width: 768px) {
    padding-left: 42.73px;
  }
  @media (min-width: 1440px) {
    padding-left: 0;
  }
}

.main-nav__logotype {
  position: relative;
}

.main-nav__logotype::after {
  content: "";
  display: block;
  position: absolute;
  width: 2px;
  height: 32px;
  left: 130px;
  top: 10px;
  border-radius: 1px;
  background-color: #eaeaea;

  @media (min-width: 768px) {
    display: none;
  }
}

@media (min-width: 768px) {
  .main-nav__logotype::before {
    content: "";
    display: block;
    position: absolute;
    height: 32px;
    width: 2px;
    left: 21px;
    top: 9px;
    border-radius: 1px;
    background-color: #eaeaea;
  }
}
@media (min-width: 1440px) {
  .main-nav__menu-toggle {
    display: none;
  }
  .main-nav__logotype::before {
    display: none;
  }
}
