.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18.59px 16px 17.59px 16px;

  @media (min-width: 768px) {
    padding: 18.59px 24px 17.59px 24px;
  }
  @media (min-width: 1120px) {
    display: none;
  }
}

.page-header__client-buttons {
  position: relative;
}

.page-header__client-buttons button,
.page-header__feedback-buttons button {
  padding-left: 16px;
}

.page-header__client-buttons {
  display: flex;
}

.page-header__feedback-buttons {
  display: none;
}

.page-header__feedback-client-buttons {
  display: flex;
}

@media (min-width: 768px) {
  .page-header__client-buttons::after {
    content: "";
    display: block;
    position: absolute;
    height: 32px;
    width: 2px;
    right: 115px;
    top: 4px;
    border-radius: 1px;
    background-color: #eaeaea;
  }
  .page-header__feedback-buttons,
  .page-header__client-buttons {
    display: flex;
    padding-left: 26px;
  }
}

@media (min-width: 1440px) {
  .page-header__client-buttons::after {
    display: none;
  }
}
